import React from 'react';
import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@material-ui/core';
import InboundDuration from './InboundDuration';
import OutboundDuration from './OutboundDuration';
import TotalCallDuration from './TotalCallDuration';

const CallDurations = () => {
  return (
    <GridContainer>
      <Grid item xs={12} sm={12} md={3} lg={3}>
        <InboundDuration />
      </Grid>
      <Grid item xs={12} sm={12} md={3} lg={3}>
        <OutboundDuration />
      </Grid>
      <Grid item xs={12} sm={12} md={3} lg={3}>
        <TotalCallDuration />
      </Grid>
    </GridContainer>
  );
};

export default CallDurations;
