import React, { useState } from 'react';
import GridContainer from '../../../@jumbo/components/GridContainer';
import PageContainer from '../../../@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '../../../@jumbo/utils/IntlMessages';
import Grid from '@material-ui/core/Grid';
import * as api from 'Api/api';
import Reports from './Reports';
import Breakout from './Breakout';
import InfoCards from './InfoCards';

const breadcrumbs = [
  { label: <IntlMessages id={'sidebar.main'} />, link: '/' },
  { label: <IntlMessages id={'sidebar.dashboard'} />, isActive: true },
];

const SamplePage = () => {
  const [queues, setQueues] = React.useState([]);
  const [value, setValue] = React.useState('');
  const [isChecked, setChecked] = useState(true);
  const agent = localStorage.getItem('agent');

  React.useEffect(() => {
    api.getQueuesByAgent(agent).then(resp => setQueues([...JSON.parse(resp.data)]));
  }, [agent]);
  const [online, setOnline] = useState([]);
  const [chosen, setChosen] = useState('');

  return (
    <PageContainer heading={<IntlMessages id={'sidebar.dashboard'} />} breadcrumbs={breadcrumbs}>
      <GridContainer>
        <Grid item xs={12}>
          <Breakout
            value={value}
            chosen={chosen}
            setChosen={setChosen}
            setValue={setValue}
            online={online}
            setOnline={setOnline}
            isChecked={isChecked}
            setChecked={setChecked}
            queues={queues}
          />
        </Grid>
        <Grid item xs={12}>
          <InfoCards value={value} setOnline={setOnline} online={online} />
        </Grid>
        <Grid item xs={12}>
          <Reports
            chosen={chosen}
            isChecked={isChecked}
            setChecked={setChecked}
            queues={queues}
            online={online}
            setOnline={setOnline}
          />
        </Grid>
      </GridContainer>
    </PageContainer>
  );
};

export default SamplePage;
