import React, { useState, useEffect } from 'react';
import { Box, Typography } from '@material-ui/core';
import * as api from 'Api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';

const TotalCallDuration = () => {
  const [response, setResponse] = useState([]);
  useEffect(() => {
    api.getTotalDuration().then(resp => {
      setResponse([...JSON.parse(resp.data.data)]);
    });
  }, []);
  return (
    <Box style={{ width: '200px' }}>
      <Box style={{ backgroundColor: '#F48B29', color: 'white' }}>
        <Typography style={{ fontFamily: 'Open Sans' }} align="center">
          <IntlMessages id={'total.duration'} />
        </Typography>
      </Box>
      <Box style={{ border: 'none', borderTop: 'none', color: '#F48B29', backgroundColor: 'white' }}>
        <Typography align="center" style={{ fontFamily: 'Open Sans' }}>
          {response.length > 0 ? response[0].total : '00:00:00'}
        </Typography>
      </Box>
    </Box>
  );
};

export default TotalCallDuration;
