import React from 'react';
import CounterCard from '@jumbo/components/Common/CounterCard';
import { ArrowForward } from '@material-ui/icons';
import IntlMessages from '@jumbo/utils/IntlMessages';

const Logout = ({ data, value }) => {
  const breaknum = data.filter(function(b) {
    return b.q_num === value && b.status === 'Logout';
  });

  return (
    <CounterCard
      icon={<ArrowForward />}
      number={
        breaknum.length > 0
          ? breaknum.map(value => {
              return value.escapetime;
            })
          : '0d 00:00:00'
      }
      label={<IntlMessages id={'logout'} />}
      labelProps={{
        fontSize: 12,
      }}
      backgroundColor={['#FFF9B0 -18.96%', '#F48B29 108.17%']}
      gradientDirection="180deg"
    />
  );
};

export default Logout;
