import React, { useState, useEffect } from 'react';
import { getQualityReport } from '../../../../../Api/api';
import dateFormat from 'dateformat';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@material-ui/core';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Chip from '@material-ui/core/Chip';

export default function QualityReport() {
  const [data, setData] = useState([]);
  useEffect(() => {
    getQualityReport().then(resp => {
      setData(JSON.parse(resp.data.data));
    });
  }, []);

  //tabloda oy verenlerin sayısını ve kimler olduğunu gösterme kısmı copyArr return edilir
  var nonRepeatingArr = [];
  var copyArr = JSON.parse(JSON.stringify(data));
  for (var i = 0; i < data.length; i++) {
    var temp = [];
    for (var j = 0; j < copyArr.length; j++) {
      if (data[i].agent === copyArr[j].agent && data[i].score === copyArr[j].score) {
        temp.push(data[i].ani);
        copyArr[j].score = null;
      }
    }
    if (temp.length > 0) {
      nonRepeatingArr.push({
        agent: data[i].agent,
        score: data[i].score,
        timeofinsertion: data[i].timeofinsertion,
        voters: temp,
      });
    }
  }

  //popover
  const [voters, setVoters] = useState([]);
  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);
  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <>
      <TableContainer style={{ maxHeight: '500px' }} component={Paper}>
        <Table stickyHeader aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>
                <IntlMessages id={'date'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'agent.name'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'votescount'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'score'} />
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {nonRepeatingArr.length > 0 &&
              nonRepeatingArr.map((item, index) => (
                <TableRow key={index}>
                  <TableCell>{dateFormat(item.timeofinsertion, 'yyyy-mm-dd HH:MM:ss')}</TableCell>
                  <TableCell>{item.agent}</TableCell>
                  <TableCell>
                    <span
                      aria-controls={open ? 'voters-menu' : undefined}
                      aria-haspopup="true"
                      style={{ cursor: 'pointer' }}
                      onClick={e => {
                        setVoters(item.voters);
                        setAnchorEl(e.currentTarget);
                      }}>
                      <b>{item.voters.length}</b>
                    </span>
                  </TableCell>
                  <TableCell>{item.score}</TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </TableContainer>
      <Menu id="voters-menu" anchorEl={anchorEl} open={open} onClose={handleClose}>
        {voters.map((item, index) => (
          <MenuItem key={index}>
            <Chip label={item} variant="outlined" />
          </MenuItem>
        ))}
      </Menu>
    </>
  );
}
