import React from 'react';
import { Grid, Box, FormControl, InputAdornment, IconButton, OutlinedInput, Button } from '@material-ui/core';
import { Cancel, Check, Close } from '@material-ui/icons';
import GridContainer from '@jumbo/components/GridContainer';
import { makeStyles } from '@material-ui/styles';
import * as api from 'Api/api';

const AddNote = ({ popupClose, setNote, id }) => {
  const useStyles = makeStyles({
    customInputLabel: {
      '& legend': {
        visibility: 'visible',
      },
    },
  });
  const classes = useStyles();
  const [input, setInput] = React.useState('');
  const eraseInput = () => {
    setInput('');
  };
  const handleChange = event => {
    setInput(event.target.value);
  };
  const saveChanges = () => {
    api.addNotes(input, id).then(() => {
      popupClose();
      setNote(input);
    });
  };
  return (
    <Box>
      <GridContainer>
        <Grid item xs={8} md={8} lg={8}>
          <FormControl sx={{ m: 1, width: '15ch' }} variant="outlined">
            <OutlinedInput
              id="standard-adornment-password"
              type={'text'}
              value={input}
              onChange={handleChange}
              classes={{ notchedOutline: classes.customInputLabel }}
              label={'Add Note'}
              endAdornment={
                <InputAdornment position="end">
                  <IconButton aria-label="toggle password visibility" onClick={eraseInput} onMouseDown={''}>
                    <Cancel />
                  </IconButton>
                </InputAdornment>
              }
              notched
            />
          </FormControl>
        </Grid>
        <Grid item xs={2} md={2} lg={2}>
          <Button
            onClick={() => {
              saveChanges();
            }}
            style={{ color: 'white', backgroundColor: '#4E8D7C' }}
            variant="contained">
            <Check />
          </Button>
        </Grid>
        <Grid item xs={2} md={2} lg={2}>
          <Button
            onClick={() => {
              popupClose();
            }}
            style={{ color: 'white', backgroundColor: '#C14545' }}
            variant="contained">
            <Close />
          </Button>
        </Grid>
      </GridContainer>
    </Box>
  );
};

export default AddNote;
