import React, { useEffect, useState } from 'react';
import IntlMessages from '@jumbo/utils/IntlMessages';
import GridContainer from '@jumbo/components/GridContainer';
import { Alert } from '@material-ui/lab';
import { getAbondenedCallsByAgent, getAbondenedCallDetails, getQueues } from 'Api/api';
import {
  Grid,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  CircularProgress,
  Box,
  Collapse,
  Typography,
  IconButton,
  Chip,
} from '@material-ui/core';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import { NotificationContainer } from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import dateFormat from 'dateformat';

export default function AbondenedCalls() {
  const [loading, setLoading] = useState(true);

  const [response, setResponse] = useState([]);
  const [detail, setDetail] = useState([]);

  const [queues, setQueues] = useState([]);

  useEffect(() => {
    setLoading(true);
    getQueues().then(resp => setQueues(JSON.parse(resp.data.data)));
    getAbondenedCallsByAgent().then(call => {
      var temp = [];
      for (let i = 0; i < JSON.parse(call.data.data).length; i++) {
        getAbondenedCallDetails(
          dateFormat(JSON.parse(call.data.data)[i].time_start, 'yyyy-mm-dd HH:MM:ss'),
          JSON.parse(call.data.data)[i].from_userpart,
        ).then(resp => {
          let data = JSON.parse(resp.data.data);
          temp.push({ i, data });
        });
      }
      setResponse(JSON.parse(call.data.data));
      setTimeout(() => {
        setDetail(temp.sort((a, b) => (a.i > b.i ? 1 : b.i > a.i ? -1 : 0)));
        setLoading(false);
      }, 2000);
    });
  }, []);
  //red #FF5C58
  //green #4E9F3D

  function Row(props) {
    const { row, index } = props;
    const [open, setOpen] = useState(false);
    return (
      <React.Fragment>
        <TableRow
          style={{ backgroundColor: detail[index].data.length > 0 ? '#4E9F3D' : '#FF5C58' }}
          sx={{
            '& > *': { borderBottom: 'unset' },
          }}>
          <TableCell>
            <IconButton
              disabled={detail[index].data.length === 0}
              aria-label="expand row"
              size="small"
              onClick={() => setOpen(!open)}>
              {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
            </IconButton>
          </TableCell>
          <TableCell component="th" scope="row">
            {dateFormat(row.time_start, 'dd-mm-yyyy HH:MM:ss')}
          </TableCell>
          <TableCell>{row.from_userpart}</TableCell>
          <TableCell>{queues.find(q => q.dn === row.q_num)?.display_name}</TableCell>
          <TableCell>
            <b>1</b>
          </TableCell>
        </TableRow>
        {detail[index].data.length > 0 && (
          <TableRow>
            <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={5}>
              <Collapse in={open} timeout="auto" unmountOnExit>
                <Box sx={{ margin: 1 }}>
                  <Typography variant="h5" gutterBottom component="div">
                    <IntlMessages id={'abondenedcalls.detail'} />
                  </Typography>
                  <Table size="small" aria-label="Details">
                    <TableHead>
                      <TableRow>
                        <TableCell>
                          <IntlMessages id={'abondenedcalls.direction'} />
                        </TableCell>
                        <TableCell>
                          <IntlMessages id={'abondenedcalls.dststarttime'} />
                        </TableCell>
                        <TableCell>
                          <IntlMessages id={'abondenedcalls.dstendtime'} />
                        </TableCell>
                        <TableCell>
                          <IntlMessages id={'abondenedcalls.time'} />
                        </TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {detail[index].data.map((item, ins) => (
                        <TableRow key={ins}>
                          <TableCell component="th" scope="row">
                            {item.direction === '-->' ? (
                              <Chip color="primary" label={<IntlMessages id={'incomingcall'} />} />
                            ) : (
                              <Chip color="secondary" label={<IntlMessages id={'outgoingcall'} />} />
                            )}
                          </TableCell>
                          <TableCell>{item.dst_start_time}</TableCell>
                          <TableCell>{item.dst_end_time}</TableCell>
                          <TableCell>{item.calltime}</TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </Box>
              </Collapse>
            </TableCell>
          </TableRow>
        )}
      </React.Fragment>
    );
  }
  return (
    <>
      <GridContainer>
        <Grid item xs={12}>
          <NotificationContainer />
          {loading ? (
            <div style={{ textAlign: 'center', marginTop: '10px' }}>
              <CircularProgress />
            </div>
          ) : (
            <TableContainer style={{ maxHeight: '500px' }} component={Paper}>
              <Table stickyHeader aria-label="Abondened Calls Table">
                <TableHead>
                  <TableRow>
                    <TableCell />
                    <TableCell>
                      <IntlMessages id={'abondenedcalls.timeofevent'} />
                    </TableCell>
                    <TableCell>
                      <IntlMessages id={'abondenedcalls.fromuserpart'} />
                    </TableCell>
                    <TableCell>
                      <IntlMessages id={'queue'} />
                    </TableCell>
                    <TableCell>
                      <IntlMessages id={'abondenedcalls.count'} />
                    </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {response.length > 0 && response.map((item, index) => <Row key={index} index={index} row={item} />)}
                </TableBody>
              </Table>
            </TableContainer>
          )}
        </Grid>
      </GridContainer>
      <div style={{ marginTop: '10px' }}>
        <Alert severity="info" style={{ marginBottom: '10px' }}>
          <IntlMessages id={'abondenedcalls.info.red'} />
        </Alert>
        <Alert severity="info">
          <IntlMessages id={'abondenedcalls.info.green'} />
        </Alert>
      </div>
    </>
  );
}
