import React, { useEffect, useState } from 'react';
import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@material-ui/core';
import DataTable from './Table/DataTable';
import * as api from 'Api/api';
import OtherStatistics from '../OtherStatistics';

const ARSL = () => {
  const [serviceLevel, setServiceLevel] = useState([]);

  //const [ar, setAR] = useState([]);
  useEffect(() => {
    fetch();
    return () => {
      setServiceLevel([]);
    };
  }, []);
  const fetch = () => {
    api.getServiceLevel().then(resp => setServiceLevel([...JSON.parse(resp.data)]));
  };
  console.log(serviceLevel);
  return (
    <GridContainer>
      <Grid item xs={12} md={12}>
        <DataTable data={serviceLevel} />
      </Grid>
      <Grid item xs={12} md={12}>
        <OtherStatistics />
      </Grid>
    </GridContainer>
  );
};

export default ARSL;
/**   <DataTable ar={ar[0]} serviceLevel={serviceLevel[0]} serviceLevel2={serviceLevel2[0]} />*/
