import React, { useEffect, useState,useContext } from 'react';
import * as api from 'Api/api';
import { PhoneCallback } from '@material-ui/icons';
import CmtCard from '@coremat/CmtCard';
import CmtCardHeader from '@coremat/CmtCard/CmtCardHeader';
import { Typography } from '@material-ui/core';
import IntlMessages from '@jumbo/utils/IntlMessages';
import AppContext from '@jumbo/components/contextProvider/AppContextProvider/AppContext';
const Talking = ({ value }) => {
  const [talking, setTalking] = useState('');
  useEffect(() => {
    const interval = setInterval(() => {
      api
        .getAgentConnection()
        .then(resp => {
          setTalking(resp.data);
        })
        .catch(function(error) {
          console.log(error.status);
        });
    }, 5000);
    return () => clearInterval(interval);
  }, []);

  /*  const array =
    talking.length > 0 || talking !== null || typeof talking !== undefined
      ? talking.filter(function(t) {
          return t.connectionNumber !== '-';
        })
      : [];*/
  const {setTabValues} = useContext(AppContext);
  return (
    <CmtCard style={{cursor:"pointer"}} onClick={() => setTabValues(5)}>
      <CmtCardHeader
        icon={<PhoneCallback fontSize="medium" style={{ color: '#4E8D7C' }} />}
        title={
          <Typography style={{ fontFamily: 'Open Sans', color: '#4E8D7C', fontWeight: 'bolder' }}>
            <IntlMessages id={'box.talking'} />: {talking !== '' ? talking : '0'}
          </Typography>
        }
      />
    </CmtCard>
  );
};

export default Talking;
