import React, { useState, useEffect } from 'react';
import { AppBar, Toolbar, Typography, Paper } from '@material-ui/core';
import GridContainer from '@jumbo/components/GridContainer';
import ReactSpeedMeter from 'react-d3-speedometer';
import * as api from 'Api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';
const Statistics = () => {
  const [data, setServiceLevel] = useState([]);
  const agent = localStorage.getItem('agent');
  let ar_ = 0,
    sl = 0,
    sl2 = 0;
  useEffect(() => {
    fetch();
    return () => {
      setServiceLevel([]);
    };
  }, []);
  const fetch = () => {
    api.getServiceLevel().then(resp => setServiceLevel([...JSON.parse(resp.data)]));
  };
  if (data !== undefined && data.length > 0) {
    const cancelled = data.find(o => o.reason_noanswerdesc === 'Cancelled');
    const answered = data.find(o => o.reason_noanswerdesc === 'Answered');
    const pollexpired = data.find(o => o.reason_noanswerdesc === 'Poll expired');
    const answershort = data.find(o => o.reason_noanswerdesc === `${agent}`);
    /*
      sl = (Number(answershort.count) / (Number(cancelled.count) + Number(answered.count))) * 100;
      sl2 =
        (Number(answershort.count) / (Number(answered.count) + Number(cancelled.count) - Number(pollexpired.count))) * 100;
      ar_ = (Number(answered.count) / (Number(answered.count) + Number(cancelled.count))) * 100;*/
    sl =
      ((typeof answershort !== 'undefined' ? Number(answershort.count) : 0) /
        ((typeof cancelled !== 'undefined' ? Number(cancelled.count) : 0) +
          (typeof answered !== 'undefined' ? Number(answered.count) : 0))) *
      100;
    sl2 =
      ((typeof answershort !== 'undefined' ? Number(answershort.count) : 0) /
        ((typeof answered !== 'undefined' ? Number(answered.count) : 0) +
          (typeof cancelled !== 'undefined' ? Number(cancelled.count) : 0) -
          (typeof pollexpired !== 'undefined' ? Number(pollexpired.count) : 0))) *
      100;
    ar_ =
      ((typeof answered !== 'undefined' ? Number(answered.count) : 0) /
        ((typeof answered !== 'undefined' ? Number(answered.count) : 0) +
          (typeof cancelled !== 'undefined' ? Number(cancelled.count) : 0))) *
      100;
  }

  return (
    <>
      <Paper style={{ minHeight: 200 }}>
        <AppBar position="static">
          <Toolbar variant="dense">
            <Typography style={{ fontFamily: 'Open Sans' }}>
              <IntlMessages id={'productivity'} />
            </Typography>
          </Toolbar>
        </AppBar>
        <GridContainer>
          <ReactSpeedMeter
            width={170}
            paddingVertical={20}
            paddingHorizontal={20}
            currentValueText={`AR: ${ar_.toFixed(2)}%`}
            maxValue={100}
            value={Number(ar_.toFixed(2))}
          />

          <ReactSpeedMeter
            width={170}
            paddingVertical={20}
            paddingHorizontal={20}
            currentValueText={`SL: ${sl.toFixed(2)}%`}
            maxValue={100}
            value={Number(sl.toFixed(2))}
          />

          <ReactSpeedMeter
            width={170}
            paddingVertical={20}
            paddingHorizontal={20}
            currentValueText={`SL2: ${sl2.toFixed(2)}%`}
            maxValue={100}
            value={Number(sl2.toFixed(2))}
          />
        </GridContainer>
      </Paper>
    </>
  );
};

export default Statistics;
