import React, { useEffect, useState } from 'react';
import { Box, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography } from '@material-ui/core';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Rating from 'react-star-ratings';
import * as api from 'Api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';

const Summary = () => {
  const [score, setScore] = useState([]);
  useEffect(() => {
    api.showRatingGroups().then(resp => setScore([...JSON.parse(resp.data.data)]));
  }, []);
  const useStyles = makeStyles(theme => ({
    root: {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    table: {
      width: 200,
    },
  }));
  const classes = useStyles();

  return (
    <Box>
      <Typography component={'h2'} align="center" style={{ fontFamily: 'Open Sans' }}>
        <IntlMessages id={'summary'} />
      </Typography>
      <TableContainer component={Paper} className={classes.root}>
        <Table className={classes.table} size="small">
          <TableHead>
            <TableRow>
              <TableCell align="center">
                <IntlMessages id={'rating'} />
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {score.length > 0 &&
              score.map((row, i) => (
                <TableRow key={i}>
                  <TableCell>
                    <Rating starDimension="20px" starRatedColor="#F48B29" rating={Number(row.score)} numberOfStars={5} />(
                    {row.count}) <IntlMessages id={'people.message'} />
                  </TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Box>
  );
};

export default Summary;
