import React from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import IntlMessages from '@jumbo/utils/IntlMessages';

const DataTable = ({ data }) => {
  let sl = 0;
  let sl2 = 0;
  let ar_ = 0;

  const useStyles = makeStyles(theme => ({
    root: {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
      fontFamily: 'Open Sans',
    },
    table: {
      minWidth: 650,
      boxShadow: 3,
      border: '1px',
    },
    tableHead: {
      fontWeight: 'bolder',
      fontSize: 14,
      color: theme.palette.common.white,
      backgroundColor: theme.palette.background.default,
    },
  }));

  const classes = useStyles();
  const agent = localStorage.getItem('agent');
  if (data !== undefined && data.length > 0) {
    const cancelled = data.find(o => o.reason_noanswerdesc === 'Cancelled');
    const answered = data.find(o => o.reason_noanswerdesc === 'Answered');
    const pollexpired = data.find(o => o.reason_noanswerdesc === 'Poll expired');
    const answershort = data.find(o => o.reason_noanswerdesc === `${agent}`);

    sl =
      ((typeof answershort !== 'undefined' ? Number(answershort.count) : 0) /
        ((typeof cancelled !== 'undefined' ? Number(cancelled.count) : 0) +
          (typeof answered !== 'undefined' ? Number(answered.count) : 0))) *
      100;
    sl2 =
      ((typeof answershort !== 'undefined' ? Number(answershort.count) : 0) /
        ((typeof answered !== 'undefined' ? Number(answered.count) : 0) +
          (typeof cancelled !== 'undefined' ? Number(cancelled.count) : 0) -
          (typeof pollexpired !== 'undefined' ? Number(pollexpired.count) : 0))) *
      100;
    ar_ =
      ((typeof answered !== 'undefined' ? Number(answered.count) : 0) /
        ((typeof answered !== 'undefined' ? Number(answered.count) : 0) +
          (typeof cancelled !== 'undefined' ? Number(cancelled.count) : 0))) *
      100;
  }
  return (
    <TableContainer component={Paper} className={classes.root}>
      <Table className={classes.table} size="small" aria-label="a dense table">
        <TableHead>
          <TableRow>
            <TableCell align="center">
              <IntlMessages id={'ar'} />
            </TableCell>
            <TableCell align="center">
              <IntlMessages id={'sl'} />
            </TableCell>
            <TableCell align="center">
              <IntlMessages id={'sl'} /> 2
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          <TableRow>
            <TableCell align="center">{ar_.toFixed(2)}%</TableCell>
            <TableCell align="center">{sl.toFixed(2)}%</TableCell>
            <TableCell align="center">{sl2.toFixed(2)}%</TableCell>
          </TableRow>
        </TableBody>
      </Table>
    </TableContainer>
  );
};

export default DataTable;
/**   
 *  <TableCell component="th" scope="row">
            {row.name}
          </TableCell>
          <TableCell align="right">{row.calories}</TableCell>
          <TableCell align="right">{row.fat}</TableCell>
          <TableCell align="right">{row.carbs}</TableCell>
          <TableCell align="right">{row.protein}</TableCell> */
