import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Queues from './Queues';
import ARSL from './ARSL';
import PreviousCalls from './PreviousCalls';
import AbondenedCalls from './AbondenedCalls';
import IntlMessages from '@jumbo/utils/IntlMessages';
import CustomerPage from 'routes/Pages/CustomerPage';
import ActiveCalls from './ActiveCalls';
import QualityReport from './QualityReport';
import AppContext from '@jumbo/components/contextProvider/AppContextProvider/AppContext';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Box
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      {...other}>
      {value === index && (
        <Box p={3}>
          <Typography component={'span'}>{children}</Typography>
        </Box>
      )}
    </Box>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `scrollable-auto-tab-${index}`,
    'aria-controls': `scrollable-auto-tabpanel-${index}`,
  };
}

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    width: '100%',
    backgroundColor: theme.palette.background.paper,
  },
}));

export default function ScrollableTabsButtonAuto({ queues, isChecked, setChecked, online, setOnline, chosen }) {
  const classes = useStyles();
  const { tabValues, setTabValues } = useContext(AppContext);

  const handleChange = (event, newValue) => {
    setTabValues(newValue);
  };

  return (
    <Box className={classes.root}>
      <AppBar position="static" color="default">
        <Tabs
          value={tabValues}
          onChange={handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="scrollable"
          scrollButtons="auto"
          aria-label="scrollable auto tabs example">
          <Tab label={<IntlMessages id={'queues'} />} {...a11yProps(0)} />
          <Tab label={<IntlMessages id={'statistic'} />} {...a11yProps(1)} />
          <Tab label={<IntlMessages id={'previous.call'} />} {...a11yProps(2)} />
          <Tab label={<IntlMessages id={'customer.satisfaction.table'} />} {...a11yProps(3)} />
          <Tab label={<IntlMessages id={'abondenedcalls'} />} {...a11yProps(4)} />
          <Tab label={<IntlMessages id={'activecalls'} />} {...a11yProps(5)} />
          <Tab label={<IntlMessages id={'survey.report'} />} {...a11yProps(6)} />
        </Tabs>
      </AppBar>
      <TabPanel value={tabValues} index={0}>
        <Queues online={online} setOnline={setOnline} queues={queues} isChecked={isChecked} setChecked={setChecked} />
      </TabPanel>
      <TabPanel value={tabValues} index={1}>
        <ARSL />
      </TabPanel>
      <TabPanel value={tabValues} index={2}>
        <PreviousCalls chosen={chosen} />
      </TabPanel>
      <TabPanel value={tabValues} index={3}>
        <CustomerPage />
      </TabPanel>
      <TabPanel value={tabValues} index={4}>
        <AbondenedCalls />
      </TabPanel>
      <TabPanel value={tabValues} index={5}>
        <ActiveCalls />
      </TabPanel>
      <TabPanel value={tabValues} index={6}>
        <QualityReport />
      </TabPanel>
    </Box>
  );
}
