import React from 'react';
import Timer from './Timer';

const TimerPage = ({ response, breakname }) => {
  const time = new Date();

  time.setSeconds(time.getSeconds() + 60 * Number(response[0].period));

  return (
    <>
      <Timer totaltime={Number(response[0].period)} breakname={breakname} expiryTimestamp={time} />
    </>
  );
};

export default TimerPage;
