import React from 'react';
import CounterCard from '@jumbo/components/Common/CounterCard';
import { ArrowBack } from '@material-ui/icons';
import IntlMessages from '@jumbo/utils/IntlMessages';

const Login = ({ data, value }) => {
  const breaknum = data.filter(function(b) {
    return b.q_num === value && b.status === 'Login';
  });

  return (
    <CounterCard
      icon={<ArrowBack />}
      number={
        breaknum.length > 0
          ? breaknum.map(value => {
              return value.escapetime;
            })
          : '0d 00:00:00'
      }
      label={<IntlMessages id={'login'} />}
      labelProps={{
        fontSize: 12,
      }}
      backgroundColor={['#8AB6D6 -18.96%', '#2978B5 108.17%']}
      gradientDirection="180deg"
    />
  );
};

export default Login;
