import React, { useEffect, useState } from 'react';
import Table from './Table';
import GridContainer from '@jumbo/components/GridContainer';
import { Grid, Typography } from '@material-ui/core';
import * as api from 'Api/api';
import Progress from '../../../Progress';
import IntlMessages from '@jumbo/utils/IntlMessages';
const Queues = ({ queues, isChecked, setChecked, online, setOnline }) => {
  const agent = localStorage.getItem('agent');
  const token = localStorage.getItem('token');
  const [loading, setLoading] = useState(true);
  useEffect(() => {
    api.qstatus({ agentDn: agent, token: token }).then(resp => {
      setLoading(false);
      setOnline(resp);
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const data = queues.map(dataValue => ({
    queue_name: dataValue.queue_display_name,
    queue_number: dataValue.queue_dn,
    isChosen: online.includes(dataValue.queue_dn),
  }));
  // console.log(online);
  return (
    <GridContainer>
      {loading ? (
        <Progress />
      ) : (
        <Grid item xs={12} md={12} lg={12}>
          {data.length > 0 ? (
            <>
              <Table data={data} online={online} setOnline={setOnline} isChecked={isChecked} setChecked={setChecked} />
            </>
          ) : (
            <Typography component="span" style={{ fontFamily: 'Open Sans' }}>
              <IntlMessages id="no.queue" />
            </Typography>
          )}
        </Grid>
      )}
    </GridContainer>
  );
};

export default Queues;
/**  */
