import React, { useEffect, useState } from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { getActiveCalls, getQueuesByAgent } from '../../../../../Api/api';
import dateFormat from 'dateformat';

function ActiveCalls() {
  const [activeCalls, setActiveCalls] = useState([]);
  const [queues, setQueues] = useState([]);
  const [queueNumbers, setQueueNumbers] = useState([]);
  const agent = localStorage.getItem('agent');
  useEffect(() => {
    getQueuesByAgent(agent).then(resp => {
      setQueues(JSON.parse(resp.data));
      setQueueNumbers(JSON.parse(resp.data).map(item => item.queue_dn));
    });
    getActiveCalls().then(resp => {
      var { callCount, ...obj } = resp.data;
      var arr = Object.keys(obj).map(key => obj[key]);
      setActiveCalls(arr);
    });
  }, [agent]);
  const filtered = activeCalls.filter(item =>
    item.Queue !== undefined
      ? queueNumbers.includes(item.Queue)
      : item.direction.includes('inbound')
      ? queueNumbers.includes(item.InternalParty)
      : item.Data.sip_displayname === agent,
  );
  return (
    <TableContainer component={Paper}>
      <Table stickyHeader aria-label="Active Calls Table">
        <TableHead>
          <TableRow>
            <TableCell>
              <IntlMessages id={'caller'} />
            </TableCell>
            <TableCell>
              <IntlMessages id={'called'} />
            </TableCell>
            <TableCell>
              <IntlMessages id={'queue'} />
            </TableCell>
            <TableCell>
              <IntlMessages id={'start.time'} />
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {filtered.length > 0 ? (
            filtered.map((item, index) => (
              <TableRow key={index}>
                <TableCell>
                  {item.direction.includes('inbound') ? item.Data?.caller_name : item.Data?.lookup_displayname}
                </TableCell>
                <TableCell>{item.direction.includes('inbound') ? item.Data?.inbound_did : item?.ExternalParty}</TableCell>
                <TableCell>
                  {item.Queue !== undefined
                    ? queues.find(q => q.queue_dn === item?.Queue)?.queue_display_name
                    : item.direction.includes('inbound')
                    ? queues.find(q => q.queue_dn === item?.InternalParty)?.queue_display_name
                    : '-'}
                </TableCell>
                <TableCell>
                  {dateFormat(convertUTCDateToLocalDate(new Date(item?.AnsweredAt)), 'dd/mm/yyyy HH:MM:ss')}
                </TableCell>
              </TableRow>
            ))
          ) : (
            <TableRow>
              <TableCell align="center" colSpan={4}>
                <IntlMessages id={'noactivecalls'} />
              </TableCell>
            </TableRow>
          )}
        </TableBody>
      </Table>
    </TableContainer>
  );
}

export default ActiveCalls;

function convertUTCDateToLocalDate(date) {
  var newDate = new Date(date.getTime() + date.getTimezoneOffset() * 60 * 1000);

  var offset = date.getTimezoneOffset() / 60;
  var hours = date.getHours();

  newDate.setHours(hours - offset);

  return newDate;
}
