import React, { useEffect, useState } from 'react';
import * as api from 'Api/api';
import { PauseCircleOutline } from '@material-ui/icons';
import CmtCard from '@coremat/CmtCard';
import CmtCardHeader from '@coremat/CmtCard/CmtCardHeader';
import { Typography } from '@material-ui/core';
import IntlMessages from '@jumbo/utils/IntlMessages';

const Waiting = ({ value }) => {
  const [waiting, setWaiting] = useState([]);

  useEffect(() => {
    api.getQueueList().then(resp => setWaiting(resp.data.data));
  }, []);
  const array =
    waiting.length > 0
      ? waiting.filter(function(w) {
          return w.waitingNumber !== '-' && w.queueNumber === value;
        })
      : 0;
  return (
    <CmtCard>
      <CmtCardHeader
        icon={<PauseCircleOutline fontSize="medium" style={{ color: '#FA744F' }} />}
        title={
          <Typography style={{ fontFamily: 'Open Sans', color: '#FA744F', fontWeight: 'bolder' }}>
            <IntlMessages id={'box.waiting'} />: {array.length}
          </Typography>
        }
      />
    </CmtCard>
  );
};

export default Waiting;
