import React, { useEffect, useState } from 'react';
import { Box, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@material-ui/core';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Rating from 'react-star-ratings';
import * as api from 'Api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';

const CustomerTable = () => {
  const [score, setScore] = useState([]);
  useEffect(() => {
    api.showRatingOfCustomer().then(resp => setScore([...JSON.parse(resp.data.data)]));
  }, []);
  const useStyles = makeStyles(theme => ({
    root: {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    table: {
      width: 400,
    },
  }));
  const classes = useStyles();

  return (
    <Box>
      <TableContainer component={Paper} className={classes.root}>
        <Table className={classes.table} size="small">
          <TableHead>
            <TableRow>
              <TableCell align="center">
                <IntlMessages id={'agent.number'} />
              </TableCell>
              <TableCell align="center">
                <IntlMessages id={'called'} />
              </TableCell>
              <TableCell style={{ width: '800px' }} align="center">
                <IntlMessages id={'rating'} />
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {score.length > 0 &&
              score.map((row, i) => (
                <TableRow key={i}>
                  <TableCell>{row.agent}</TableCell>
                  <TableCell>{row.ani}</TableCell>
                  <TableCell>
                    <Rating starDimension="15px" starRatedColor="#F48B29" rating={Number(row.score)} numberOfStars={5} />
                  </TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Box>
  );
};

export default CustomerTable;
