import React, { useEffect, useState } from 'react';
import { FormControl, Select, MenuItem, Chip } from '@material-ui/core';
import * as api from 'Api/api';

const TagSelection = ({ id, tagtext, chosen, msg }) => {

  const color = ['#5584AC', '#4E8D7C', '#DE834D'];
  const textList = msg.map(value => ({
    name: value.tagmassage,
    color: 'primary',
    bgColor: color[msg.indexOf(value)],
  }));
  const currentMsg = tagtext !== '' ? {name:tagtext, color: 'primary',bgColor:color[0]}: '';

  const [age, setAge] = useState('');
  const handleChange = event => {
    api.TagByID(event.target.value, id).then(resp => {
      setAge(event.target.value);
    });
  };

  useEffect(()=>{
    setAge(tagtext);
  },[tagtext]);
  return (
    <>
      <FormControl sx={{ m: 1, minWidth: 80 }}>
        <Select
          labelId="demo-simple-select-autowidth-label"
          id="demo-simple-select-autowidth"
          value={age}
          onChange={handleChange}
          autoWidth>
          <MenuItem value=''>
            <em>None</em>
          </MenuItem>
          {currentMsg !== '' && 
          <MenuItem value={currentMsg.name}>
          <Chip size="small" color={currentMsg.color} label={currentMsg.name} style={{ backgroundColor: currentMsg.bgColor }} />
          </MenuItem>}
          {textList.map((value, i) => (
            <MenuItem key={i} value={value.name}>
              <Chip size="small" color={value.color} label={value.name} style={{ backgroundColor: value.bgColor }} />
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </>
  );
};

export default TagSelection;
