import React, { useState, useEffect } from 'react';
import { Box, Typography } from '@material-ui/core';
import * as api from 'Api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';

const InboundDuration = () => {
  const [response, setResponse] = useState([]);
  useEffect(() => {
    api.getInboundCallDuration().then(resp => {
      setResponse([...JSON.parse(resp.data.data)]);
    });
  }, []);

  return (
    <Box style={{ width: '200px' }}>
      <Box style={{ backgroundColor: '#4E8D7C', color: 'white' }}>
        <Typography style={{ fontFamily: 'Open Sans' }} align="center">
          <IntlMessages id={'inbound.duration'} />
        </Typography>
      </Box>
      <Box style={{ border: 'none', borderTop: 'none', color: '#4E8D7C', backgroundColor: 'white' }}>
        <Typography align="center" style={{ fontFamily: 'Open Sans' }}>
          {response.length > 0 ? response[0].total_inbound_time : '00:00:00'}
        </Typography>
      </Box>
    </Box>
  );
};

export default InboundDuration;
