import React, { useEffect } from 'react';

import { Paper, Typography } from '@material-ui/core';
import { Bar, Cell, BarChart, CartesianGrid, Legend, ResponsiveContainer, Tooltip, XAxis, YAxis } from 'recharts';

import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IntlMessages from '@jumbo/utils/IntlMessages';

const ChartData = ({ data }) => {
  const keys = data.flatMap(Object.keys);
  const [chartItems, setChartItems] = React.useState([]);
  const colors = ['#4E8D7C', '#FA744F', '#C14545', '#2978B5', '#F48B29', '#612897', '#FFFDA2', '#F48B29'];

  useEffect(() => {
    // eslint-disable-next-line
      data.map((val, i) => {
      // eslint-disable-next-line
        Object.keys(val).map(function(key, index){
        var hms = val[key];

        var a = hms.split(':');
        var seconds = +a[0] * 60 * 60 + +a[1] * 60 + +a[2];
        val[key] = seconds;

        setChartItems(oldArray => [
          ...oldArray,
          {
            name: keys[index],
            seconds: val[key],
          },
        ]);
      });
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);

  return (
    <Paper>
      <AppBar position="static">
        <Toolbar variant="dense">
          <Typography style={{ fontFamily: 'Open Sans' }}>
            <IntlMessages id={'status.dist'} />
          </Typography>
        </Toolbar>
      </AppBar>
      <ResponsiveContainer width="100%" height={400}>
        <BarChart layout="vertical" data={chartItems} margin={{ top: 10, right: 5, left: 15, bottom: 0 }}>
          <XAxis type="number" dataKey="seconds" />

          <YAxis dataKey="name" type="category" />
          <CartesianGrid strokeDasharray="3 3" />
          <Tooltip labelStyle={{ color: 'black' }} itemStyle={{ color: 'black' }} cursor={false} />
          <Legend payload={[{ value: <IntlMessages id={'seconds'} />, type: 'line' }]} />
          <Bar dataKey={'seconds'} fill="url(#color1)">
            {chartItems.length > 0 && chartItems.map((entry, index) => <Cell key={`cell-${index}`} fill={colors[index]} />)}
          </Bar>
        </BarChart>
      </ResponsiveContainer>
    </Paper>
  );
};

export default ChartData;
