import React from 'react';
import { LocalPhone } from '@material-ui/icons';
import CmtCard from '@coremat/CmtCard';
import CmtCardHeader from '@coremat/CmtCard/CmtCardHeader';
import { Typography } from '@material-ui/core';
import IntlMessages from '@jumbo/utils/IntlMessages';
const Ready = ({ stat }) => {
  const array = stat.filter(function(a) {
    return a.states === 'Login';
  });

  return (
    <CmtCard>
      <CmtCardHeader
        icon={<LocalPhone fontSize="medium" style={{ color: '#2978B5' }} />}
        title={
          <Typography style={{ fontFamily: 'Open Sans', color: '#2978B5', fontWeight: 'bolder' }}>
            <IntlMessages id={'box.ready'} />: {array.length}
          </Typography>
        }
      />
    </CmtCard>
  );
};

export default Ready;
