import React from 'react';
import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@material-ui/core';
import LongestCaller from './LongestCaller';
import LongestCalled from './LongestCalled';
import MostCalled from './MostCalled';
import MostCaller from './MostCaller';
import MostBreakTime from './GetMostBreakTime';

const OtherStatistics = () => {
  return (
    <GridContainer>
      <Grid item xs={12} sm={12} md={3} lg={3}>
        <LongestCaller />
      </Grid>
      <Grid item xs={12} sm={12} md={3} lg={3}>
        <LongestCalled />
      </Grid>
      <Grid item xs={12} sm={12} md={3} lg={3}>
        <MostCalled />
      </Grid>
      <Grid item xs={12} sm={12} md={3} lg={3}>
        <MostCaller />
      </Grid>
      <Grid item xs={12} sm={12} md={3} lg={3}>
        <MostBreakTime />
      </Grid>
    </GridContainer>
  );
};

export default OtherStatistics;
