import React, { useState, useEffect } from 'react';
import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@material-ui/core';
import Talking from './Talking';
import Waiting from './Waiting';
import Break from './Break';
import * as api from 'Api/api';
import Ready from './Ready';

const InfoCards = ({ value, online, setOnline }) => {
  const [stat, setStat] = useState([]);
  useEffect(() => {
    api.getAgentStatus(value).then(resp => setStat([...JSON.parse(resp.data.data)]));
  }, [online, value]);

  return (
    <GridContainer>
      <Grid item xs={12} sm={6} md={3} lg={3}>
        <Waiting value={value} online={online} setOnline={setOnline} />
      </Grid>
      <Grid item xs={12} sm={6} md={3} lg={3}>
        <Talking online={online} setOnline={setOnline} />
      </Grid>
      <Grid item xs={12} sm={6} md={3} lg={3}>
        <Break stat={stat} online={online} setOnline={setOnline} />
      </Grid>
      <Grid item xs={12} sm={6} md={3} lg={3}>
        <Ready stat={stat} online={online} setOnline={setOnline} />
      </Grid>
    </GridContainer>
  );
};

export default InfoCards;
