import React from 'react';
import CounterCard from '@jumbo/components/Common/CounterCard';
import { AccessAlarm } from '@material-ui/icons';
import IntlMessages from '@jumbo/utils/IntlMessages';

const Ekstra1 = ({ data, value }) => {
  const breaknum = data.filter(function(b) {
    return b.q_num === value && b.status === 'Ekstra 1';
  });

  return (
    <CounterCard
      icon={<AccessAlarm />}
      number={
        breaknum.length > 0
          ? breaknum.map(value => {
              return value.escapetime;
            })
          : '0d 00:00:00'
      }
      label={<IntlMessages id={'shortbreak'} />}
      labelProps={{
        fontSize: 12,
      }}
      backgroundColor={['#F79071 -18.96%', '#FA744F 108.17%']}
      gradientDirection="180deg"
    />
  );
};

export default Ekstra1;
