import React from 'react';
import { useTimer } from 'react-timer-hook';
import './style.css';
import { CircularProgressbar, buildStyles } from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';
import StopWatch from './StopWatch';

const Timer = ({ breakname, expiryTimestamp, totaltime }) => {
  const [expired, setExpired] = React.useState(false);
  const { minutes, seconds } = useTimer({ expiryTimestamp, onExpire: () => setExpired(true) });

  const totalSeconds = totaltime * 60;
  const value = ((minutes * 60 + seconds) / totalSeconds) * 100;

  return (
    <div style={{ textAlign: 'center' }}>
      <h1 className={expired ? 'lineUp' : ''} style={expired ? { color: '#C14545' } : { color: '#2978B5' }}>
        {expired ? 'Süre Doldu' : `MOLA: ${breakname}`}
      </h1>
      {expired ? (
        <StopWatch value={value} />
      ) : (
        <CircularProgressbar
          value={value}
          text={`${minutes < 10 ? `0${minutes}` : minutes}:${seconds < 10 ? `0${seconds}` : seconds}`}
          styles={buildStyles({
            pathColor: minutes === 0 && seconds < 10 ? '#C14545' : '#2978B5',
          })}
        />
      )}
    </div>
  );
};

export default Timer;
/**<span>{minutes < 10 ? `0${minutes}` : minutes}</span>:<span>{seconds < 10 ? `0${seconds}` : seconds}</span> */
