import React, { useEffect, useState } from 'react';
import * as api from 'Api/api';

import { Typography, Table, TableBody, TableCell, TableContainer, TableRow, Paper, TableHead } from '@material-ui/core';
import IntlMessages from '@jumbo/utils/IntlMessages';

const LongestCaller = () => {
  const [response, setResponse] = useState([]);
  useEffect(() => {
    api.getInternalsSummaryLongestCaller().then(resp => {
      setResponse([...JSON.parse(resp.data.data)]);
    });
  }, []);
  const rows = response.map(value => ({
    duration: value.en_uzun_aranan_süre,
    number: value.en_uzun_arayan_numara,
  }));
  for (var i = 0; i < 5 - response.length; i++) {
    rows.push({ duration: '-', number: '-' });
  }
  return (
    <TableContainer component={Paper}>
      <Typography style={{ fontFamily: 'Open Sans' }} component={'div'} variant="h4" align="center">
        <IntlMessages id={'longest.caller.title'} />
      </Typography>
      <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
        <TableHead>
          <TableRow style={{ fontFamily: 'Open Sans' }}>
            <TableCell>
              <IntlMessages id={'numbers'} />
            </TableCell>
            <TableCell align="right">
              <IntlMessages id={'duration'} />
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row, index) => (
            <TableRow key={index} sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
              <TableCell component="th" scope="row">
                {row.number}
              </TableCell>
              <TableCell align="right">{row.duration}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
};

export default LongestCaller;
