import React from 'react';
import { Redirect, Route, Switch } from 'react-router';
import { useLocation } from 'react-router-dom';
import SamplePage from './Pages/SamplePage';
import Error404 from './Pages/404';
import Login from './Auth/Login';
import { PrivateRouteLogin } from '../Private/PrivateRouteLogin';
import Register from './Auth/Register';
import ForgotPasswordPage from './Auth/ForgotPassword';
import ChartPage from './Pages/ChartPage';
import CustomerPage from './Pages/CustomerPage';

const RestrictedRoute = ({ component: Component, ...rest }) => {
  return (
    <Route
      {...rest}
      render={props =>
        localStorage.getItem('token') ? (
          <Component {...props} />
        ) : (
          <Redirect
            to={{
              pathname: '/signin',
              state: { from: props.location },
            }}
          />
        )
      }
    />
  );
};

const Routes = () => {
  const addZero = value => {
    if (value < 10) {
      return '0' + value;
    } else {
      return value;
    }
  };

  const convertToLocalTimeLogin = date_ => {
    var date = new Date(date_);
    var year = date.getFullYear();
    var month = date.getMonth() + 1;
    var dt = date.getDate();
    var hour = date.getHours();
    var minutes = date.getMinutes();
    var seconds = date.getSeconds();

    return (
      +year +
      '-' +
      addZero(month) +
      '-' +
      addZero(dt) +
      ' ' +
      addZero(hour) +
      ':' +
      addZero(minutes) +
      ':' +
      addZero(seconds)
    );
  };

  if (JSON.parse(localStorage.getItem('interval')) !== null) {
    var values = JSON.parse(localStorage.getItem('interval'));
    if (convertToLocalTimeLogin(values[0]) < convertToLocalTimeLogin(new Date())) {
      localStorage.removeItem('user');
      localStorage.removeItem('role');
      localStorage.removeItem('agent');
      localStorage.removeItem('validation');
    }
  }

  const location = useLocation();

  if (location.pathname === '' || location.pathname === '/') {
    return <Redirect to={'/signin'} />;
  } else if (localStorage.getItem('login') === 200 && location.pathname === '/signin') {
    return <Redirect to={'/queues'} />;
  }

  return (
    <React.Fragment>
      <Switch>
        <RestrictedRoute path="/queues" component={SamplePage} />
        <RestrictedRoute path="/performance" component={ChartPage} />
        <PrivateRouteLogin path="/signin" component={Login} />
        <Route path="/signup" component={Register} />
        <Route path="/forgot-password" component={ForgotPasswordPage} />
        <Route path={'/customerrate'} component={CustomerPage} />
        <Route component={Error404} />
      </Switch>
    </React.Fragment>
  );
};

export default Routes;
