const axios = require('axios').default;
const token = localStorage.getItem('token');
const agent = localStorage.getItem('agent');
const url = window.appsettings.appUrl || process.env.REACT_APP_URL;
const port = window.appsettings.appPort || process.env.REACT_APP_PORT;

const date = new Date();
const day = date.getDate();
const month = date.getMonth() + 1;
const year = date.getFullYear();

const addZero = val => {
  if (val < 0) {
    return '0' + val;
  } else {
    return val;
  }
};
const param = year + '-' + addZero(month) + '-' + addZero(day);
//login
export const getUserAuth = async param => {
  const response = await axios.post(`http://${url}:${port}/loginagent`, param);

  return response;
};
//time offset TR UTC+3
export const getOffset = async () => {
  return await axios.get(`http://${url}:${port}/zone`);
};
const timeOffset = localStorage.getItem('timeoffset') || '+3';
//dashboard
export const getQueuesByAgent = async param => {
  const query = `SELECT queue_dn,queue_display_name FROM public.extensions_by_queues_view where extension_dn='${param}'`;

  const response = await axios.post(
    `http://${url}:${port}/queryresult`,
    { token: `${token}`, query: `${query}` },
    {
      headers: {
        'Content-Type': 'application/json',
      },
    },
  );
  return response.data;
};
export const getBreakChartData = async () => {
  let query = `with recursive cte1 as 
  (
  select dn.value,ivr.name from dn 
  inner join ivr on dn.iddn = ivr.fkiddn
  order by dn.value asc
  ), cte2 as (SELECT
  sum(timeofupdate - (select
  timeofupdate
  from
  callcent_ag_qstatus
  where
  ag_num = qs.ag_num
  and
  q_num = qs.q_num
  and
  qs.timeofupdate > timeofupdate
  order by timeofupdate desc
  limit 1)) as escapetime,
  (select
  is_loggedin_in_queue
  from
  callcent_ag_qstatus
  where
  ag_num = qs.ag_num
  and
  q_num = qs.q_num
  and
  qs.timeofupdate > timeofupdate
  order by timeofupdate desc
  limit 1) as mainreason,
  ag_num,
  q_num
  FROM
  callcent_ag_qstatus qs
  where
  q_num like '%'
  and
  ag_num like '%${agent}'
  and
  is_loggedin_in_queue = 0
  and
  (select
  is_loggedin_in_queue
  from
  callcent_ag_qstatus
  where
  ag_num = qs.ag_num
  and
  q_num = qs.q_num
  and
  qs.timeofupdate > timeofupdate
  order by timeofupdate desc
  limit 1) is not null
  and
  timeofupdate <= timestamp '${param} 23:59:59'
  and
  timeofupdate >= timestamp '${param} 00:00:00'
  group by mainreason,ag_num,q_num
  order by mainreason asc )    
  select 
 
  date_trunc('second',sum(coalesce((select (cte2.escapetime) where cte2.mainreason = 8072),'0 second'))) as "m8072",
  date_trunc('second',sum(coalesce((select (cte2.escapetime) where cte2.mainreason = 8073),'0 second'))) as "m8073",
  date_trunc('second',sum(coalesce((select (cte2.escapetime) where cte2.mainreason = 8074),'0 second'))) as "m8074",
  date_trunc('second',sum(coalesce((select (cte2.escapetime) where cte2.mainreason = 8075),'0 second'))) as "m8075",
  date_trunc('second',sum(coalesce((select (cte2.escapetime) where cte2.mainreason = 8076),'0 second'))) as "m8076",
  date_trunc('second',sum(coalesce((select (cte2.escapetime) where cte2.mainreason = 8077),'0 second'))) as "m8077",
  date_trunc('second',sum(coalesce((select (cte2.escapetime) where cte2.mainreason = 8078),'0 second'))) as "m8078",
  date_trunc('second',sum(coalesce((select (cte2.escapetime) where cte2.mainreason = 8079),'0 second'))) as "m8079" 
  from cte2	
  left outer join cte1 on cast(cte2.mainreason as character varying) = cte1.value
  where cte2.ag_num like '%${agent}'
  and cte2.mainreason !=1
  group by cte2.ag_num
  `;
  return axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: query,
  });
};
export const getAgentStatus = async q_num => {
  let query = `WITH RECURSIVE cte1 as (
    select 
    dn.value,
    ivr.name 
    from 
    dn 
    inner join ivr on dn.iddn = ivr.fkiddn
    ) , 
    cte2 as (
    select
    * from callcent_ag_qstatus
    where timeofupdate > date_trunc('day', timestamp '${param}') and q_num='${q_num}'
    )
  select
  dn,
  display_name,
  coalesce((
  select 
  case 
  when cte2.is_loggedin_in_queue = 0 then 'Login' 
  when cte2.is_loggedin_in_queue = 1 then 'Logout'
  else cte1.name
  end as status
  from cte2
  left outer join cte1 on cte1.value = cast(cte2.is_loggedin_in_queue as character varying)
  where ag_num = t.dn
  order by cte2.timeofupdate desc
  limit 1  
  ),'State') as states,
  date_trunc('second',coalesce(now()- (
  select 
  timeofupdate
  from cte2
  where ag_num = t.dn
  order by cte2.timeofupdate desc
  limit 1
  ),'10000 hours')) as statusTime
  from users_view t
  order by dn asc`;

  return axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: query,
  });
};
