export const getInternalsSummaryLongestCaller = async () => {
  let string = `with recursive cte1 as (
    select 
    *
    from
    cl_segments_view
    where start_time >= date_trunc('day',timestamp '${param}')
    and act!= 1
    and start_time <= date_trunc('day',timestamp '${param}' + interval '1 day')
  )
  select 
  src_display_name as en_uzun_arayan_numara,
  cast(dst_end_time-dst_answer_time as character varying) as en_uzun_aranan_süre
  from
  cte1
  where
  cte1.dst_dn ='${agent}'  
  and
  dst_answer_time is not null

  order by (dst_end_time-dst_answer_time) desc
  limit 5`;

  return axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: string,
  });
};

export const getInternalsSummaryLongestCalled = async () => {
  let string = `with recursive cte1 as (
    select 
    *
    from
    cl_segments_view
    where start_time >= date_trunc('day',timestamp '${param}')
    and start_time <= date_trunc('day',timestamp '${param}' + interval '1 day')
    and src_dn like '%${agent}'
    and dst_dn_type = 1
    and act != 1
  )
  select 
  dst_display_name as en_uzun_aranan_numara,
  cast(dst_end_time-dst_answer_time as character varying) as en_uzun_aranan_süre
  from
  cte1
  where
   dst_answer_time is not null
  order by (dst_end_time-dst_answer_time) desc
  limit 5`;

  return axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: string,
  });
};
export const getMostBreakTime = async () => {
  let string = `with recursive cte1 as (
    select dn.value,ivr.name from dn 
    inner join ivr on dn.iddn = ivr.fkiddn
    order by dn.value asc
  ), cte2 as (SELECT
    sum(timeofupdate - (select
    timeofupdate
    from
    callcent_ag_qstatus
    where
    ag_num = qs.ag_num
    and
    q_num = qs.q_num
    and
    qs.timeofupdate > timeofupdate
    order by timeofupdate desc
    limit 1)) as escapetime,
    (select
    is_loggedin_in_queue
    from
    callcent_ag_qstatus
    where
    ag_num = qs.ag_num
    and
    q_num = qs.q_num
    and
    qs.timeofupdate > timeofupdate
    order by timeofupdate desc
    limit 1) as mainreason,
    ag_num,
    q_num
    FROM
    callcent_ag_qstatus qs
    where
    q_num like '%'
    and
    ag_num like '%'
    and
    is_loggedin_in_queue = 0
    and
    (select
    is_loggedin_in_queue
    from
    callcent_ag_qstatus
    where
    ag_num = qs.ag_num
    and
    q_num = qs.q_num
    and
    qs.timeofupdate > timeofupdate
    order by timeofupdate desc
    limit 1) is not null
    and
    timeofupdate <= timestamp '${param} 23:59:59'
    and
    timeofupdate >= timestamp '${param} 00:00:00'
    group by mainreason,ag_num,q_num
    order by mainreason asc )    
    select 
    cte1.name,
	  to_char(sum(cte2.escapetime),'DDg HH24h MIm SSs')
    from cte2	
    left outer join cte1 on cast(cte2.mainreason as character varying) = cte1.value
    where cte2.ag_num = '${agent}'
	  and cte1.name is not null
    group by cte1.name
	  order by sum(cte2.escapetime) desc
	  limit 5`;

  return axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: string,
  });
};
export const getMostCalledNumber = async () => {
  let string = `with recursive cte1 as (
    select 
    *
    from
    cl_segments_view
    where start_time >= date_trunc('day',timestamp '${param}'-interval '24' hour)
    and src_dn ='${agent}' 
    and act != 1
    and dst_dn_type = 1
    order by dst_start_time desc
  )
  select 
  dst_display_name as en_cok_aranan_numara,
  count(distinct(call_id))
  from
  cte1  
  group by dst_display_name
  order by count(distinct(call_id)) desc
  limit 5`;

  return axios.post(`http://${url}:${port}/queryresult`, {
    token: localStorage.getItem('token'),
    query: string,
  });
};
