export const showBreaks = async agent => {
  const query = `with recursive cte1 as (
    select dn.value,ivr.name from dn 
    inner join ivr on dn.iddn = ivr.fkiddn
    order by dn.value asc
  ), cte2 as (SELECT to_char(
        sum(timeofupdate - (select
                  timeofupdate
                  from
                  callcent_ag_qstatus
                  where
                  ag_num = qs.ag_num
                  and
                  q_num = qs.q_num
                  and
                  qs.timeofupdate > timeofupdate
                  order by timeofupdate desc
                  limit 1)) , 'dd - HH24:MI:ss') as escapetime,
        (select
         is_loggedin_in_queue
         from
         callcent_ag_qstatus
         where
         ag_num = qs.ag_num
         and
         q_num = qs.q_num
         and
         qs.timeofupdate > timeofupdate
         order by timeofupdate desc
         limit 1) as mainreason,
        ag_num,
        q_num
        FROM
        callcent_ag_qstatus qs
        where
        ag_num like '${agent}'
        and
        is_loggedin_in_queue = 0
        and
        (select
              is_loggedin_in_queue
         from
         callcent_ag_qstatus
         where
         ag_num = qs.ag_num
         and
         q_num = qs.q_num
         and
         qs.timeofupdate > timeofupdate
         order by timeofupdate desc
         limit 1) is not null
        and
       timeofupdate <= timestamp '${param} 23:59:59'
        and
        timeofupdate >= timestamp '${param} 00:00:00'
        group by mainreason,ag_num,q_num
        order by mainreason asc )
        
        select 
        cte2.ag_num,
        cte2.q_num,
        case
        when cte2.mainreason = 0 then 'Login'
        when cte2.mainreason = 1 then 'Logout'
        else cte1.name
        end as status,
        cte2.escapetime
        from cte2
        left outer join cte1 on cast(cte2.mainreason as character varying) = cte1.value`;
  const response = await axios.post(
    `http://${url}:${port}/queryresult`,
    { token: `${token}`, query: `${query}` },
    {
      headers: {
        'Content-Type': 'application/json',
      },
    },
  );
  return response.data;
};
