import React, { useEffect, useState } from 'react';
import { makeStyles, lighten } from '@material-ui/core/styles';
import {
  TableCell,
  TableContainer,
  Table,
  TableBody,
  TableHead,
  Paper,
  TableRow,
  IconButton,
  AppBar,
  Toolbar,
  Menu,
  MenuItem,
  Box,
  Modal,
  InputLabel,
  Select,
  FormControl
} from '@material-ui/core';
import PhoneForwardedIcon from '@material-ui/icons/PhoneForwarded';
import * as api from 'Api/api';
import { NotificationContainer, NotificationManager } from 'react-notifications';
import TagSelection from './TagSelection';
import { PlayArrow, Clear, FilterList } from '@material-ui/icons';
import IntlMessages from '@jumbo/utils/IntlMessages';
import Note from './Note';
import RatingBoard from './Rating';
import AudioWaveForm from '@jumbo/utils/AudioWaveForm';

const DataTable = ({ data, chosen }) => {
  const useStyles = makeStyles(theme => ({
    root: {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
      fontFamily: 'Open Sans',
    },
    table: {
      minWidth: 200,
      boxShadow: 3,
      border: '1px',
    },
    tableHead: {
      fontWeight: 'bolder',
      fontSize: 14,
      color: theme.palette.common.white,
      backgroundColor: theme.palette.background.default,
    },
    footer: {
      color: theme.palette.common.white,
      backgroundColor: theme.palette.background.default,
      fontWeight: 'bolder',
    },
    drawer: {
      position: 'relative',
      marginLeft: 'auto',
      width: 200,
      '& .MuiBackdrop-root': {
        display: 'none',
      },
      '& .MuiDrawer-paper': {
        width: 200,
        position: 'absolute',
        minHeight: 100,
        transition: 'none !important',
      },
    },
  }));
  const useSelectStyles = makeStyles({
    select: {
      '&':{
        color:'white'
      },
      '&:before': {
        borderColor: 'white',
      },
      '&:after': {
        borderColor: 'white',
      },
      '&:not(.Mui-disabled):hover::before': {
        borderColor: 'white',
      },
    },
    icon: {
      fill: 'white',
    },
    root: {
      color: 'white',
    },
  });
  const [tags, setTags] = React.useState([]);
  const [msg, setMsg] = React.useState([]);
  let [path, setPath] = React.useState('');

  useEffect(() => {
    api.getRecordingPath().then(res => {
      setPath(res.data);
    });
  }, []);
  //listening record
  const [openAudioModal, setOpenAudioModal] = useState(false);
  const [recordpath, setRecordPath] = useState('');
  const listenRecord = dir => {
    api
      .getRecording(path, dir)
      .then(resp => resp.arrayBuffer())
      .then(buffer => {
        if (buffer !== null) {
          setRecordPath(buffer);
          setOpenAudioModal(true);
        } else {
          NotificationManager.error(<IntlMessages id={'norecording'} />, '', 3000);
        }
      })
      .catch(err => NotificationManager.error('No Recording', '', 3000));
  };
  let [recordAuth, setRecordAuth] = useState(false);
  useEffect(() => {
    api.tagMessage(chosen).then(resp => setMsg([...JSON.parse(resp.data)]));
  }, [chosen]);
  useEffect(() => {
    api.getTags().then(resp => setTags(JSON.parse(resp.data)));
  }, []);
  useEffect(() => {
    api.getAgentsRecordListenAuthByAgentId(window.localStorage.getItem('agent')).then(resp => {
      setRecordAuth(JSON.parse(resp.data.data)[0].record_listen === 'True');
    });
  }, []);

  const [filtered, setFiltered] = useState(data);
  const classes = useStyles();
  const selectClasses = useSelectStyles();
  const style = {
    boxShadow: 'rgba(0, 0, 0, 0.24) 0px 3px 8px',
    borderRadius: '8px',
    position: 'absolute',
    left: '50%',
    top: '50%',
    transform: 'translate(-50%, -50%)',
    padding: '12px 8px',
    border: '2px solid #ededed',
    backgroundColor: '#ededed',
    width: '600px',
  };
  const [direction, setDirection] = useState('all');

  var myAgentId = localStorage.getItem('agent');
  const handleDirectionChange = e => {
    setDirection(e.target.value);
    switch (e.target.value) {
      case 'all':
        setFiltered(data);
        break;
      case 'inbound':
        setFiltered(data.filter(item => item.called === myAgentId));
        break;
      case 'outbound':
        setFiltered(data.filter(item => item.caller === myAgentId));
        break;
      default:
        setFiltered(data);
        break;
    }
  };
  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);
  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };
  const handleNoTagged = () => {
    setFiltered(data.filter(item => !tags.some(t => t.call_id === item.call_id)));
    setAnchorEl(null);
  };
  const handleAll = () => {
    setFiltered(data);
    setAnchorEl(null);
  };

  const handleCall = (nums) => {
    let to = "";
    let from = "";
    if(nums[0] === myAgentId){
      from = nums[0];
      to = nums[1];
    }
    else{
      from = nums[1];
      to = nums[0];
    }
    api.MakeCall(from,to);
  };
  return (
    <div>
      <NotificationContainer />
      <TableContainer component={Paper}>
        <AppBar position="static">
          <Toolbar style={{ display: 'flex' }}>
            <FormControl style={{ width: '200px', color: 'inherit' }}>
              <InputLabel style={{ color: '#fff' }} id="callDirection">
                <IntlMessages id={'abondenedcalls.direction'} />
              </InputLabel>
              <Select
                className={selectClasses.select}
                labelId="callDirection"
                id="directionSelect"
                value={direction}
                onChange={handleDirectionChange}
                inputProps={{
                  classes: {
                    icon: selectClasses.icon
                  }
                }}>
                <MenuItem value="all">
                  <IntlMessages id={'all'} />
                </MenuItem>
                <MenuItem value="inbound">
                  <IntlMessages id={'incomingcall'} />
                </MenuItem>
                <MenuItem value="outbound">
                  <IntlMessages id={'outgoingcall'} />
                </MenuItem>
              </Select>
            </FormControl>
            <IconButton
              onClick={handleClick}
              style={{ marginLeft: 'auto' }}
              aria-haspopup="true"
              color="inherit"
              aria-label="filterButton">
              <FilterList />
            </IconButton>
          </Toolbar>
        </AppBar>

        <Menu id="filterButton" anchorEl={anchorEl} open={open} onClose={() => setAnchorEl(null)}>
          <MenuItem onClick={handleNoTagged}>
            <IntlMessages id={'no.tagged'} />
          </MenuItem>
          <MenuItem onClick={handleAll}>
            <IntlMessages id={'all'} />
          </MenuItem>
        </Menu>

        <Table className={classes.table} size="small" aria-label="a dense table">
          <caption>
            <IntlMessages id={'table.note'} />
          </caption>
          <TableHead component={'thead'} className={classes.tableHead}>
            <TableRow style={{ fontFamily: 'Open Sans' }}>
              <TableCell align="center">
                <IntlMessages id={'caller'} />
              </TableCell>
              <TableCell align="center">
                <IntlMessages id={'called'} />
              </TableCell>
              <TableCell align="center">
                <IntlMessages id={'start.time'} />
              </TableCell>
              <TableCell align="center">
                <IntlMessages id={'talking.time'} />
              </TableCell>
              <TableCell align="center">
                <IntlMessages id={'ringing.time'} />
              </TableCell>
              <TableCell align="center">
                <IntlMessages id={'end.time'} />
              </TableCell>
              <TableCell align="center">
                {' '}
                <IntlMessages id={'record'} />
              </TableCell>
              <TableCell width={'200px'} align="center">
                <IntlMessages id={'rating'} />
              </TableCell>
              <TableCell align="center">
                {' '}
                <IntlMessages id={'notes'} />
              </TableCell>
              <TableCell align="center">
                <IntlMessages id={'tag'} />
              </TableCell>
              <TableCell align="right">
                <IntlMessages id={'call'} />
              </TableCell>
            </TableRow>
          </TableHead>

          <TableBody>
            {filtered.map((row, i) => (
              <TableRow key={i}>
                <TableCell align="center">{row.caller}</TableCell>
                <TableCell align="center">{row.called}</TableCell>
                <TableCell align="center">{row.dst_start_time}</TableCell>
                <TableCell align="center">{row.talking_time}</TableCell>
                <TableCell align="center">{row.ringing_time}</TableCell>
                <TableCell align="center">{row.dst_end_time}</TableCell>
                <TableCell align="center">
                  {recordAuth ? (
                    row.coalesce !== 'no recording' ? (
                      <IconButton
                        onClick={() => listenRecord(row.coalesce)}
                        style={{ backgroundColor: '#C6D57E', color: '#406343' }}
                        size="small"
                        aria-label="listen">
                        <PlayArrow />
                      </IconButton>
                    ) : (
                      <IconButton
                        disabled
                        style={{ backgroundColor: '#FE8F8F', color: '#FF5C58' }}
                        size="small"
                        aria-label="listen">
                        <Clear />
                      </IconButton>
                    )
                  ) : (
                    <p>
                      <IntlMessages id={'noauth'} />
                    </p>
                  )}
                  {}
                </TableCell>
                <TableCell align="center">{tags.length > 0 ? <RatingBoard tags={tags} id={row.call_id} /> : ''}</TableCell>
                <TableCell align="center">
                  {tags.length > 0 ? <Note setMsg={setMsg} id={row.call_id} tags={tags} /> : ''}
                </TableCell>
                <TableCell align="center">
                  {tags.length > 0 ? (
                    <TagSelection
                      msg={msg}
                      id={row.call_id}
                      tagtext={tags.find(t => t.call_id === row.call_id)?.tagtext || ''}
                      chosen={chosen}
                    />
                  ) : (
                    ''
                  )}
                </TableCell>
                <TableCell>
                  <IconButton color="primary" aria-label="call" onClick={() =>handleCall([row.caller,row.called])}>
                    <PhoneForwardedIcon />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <Modal
        open={openAudioModal}
        onClose={() => setOpenAudioModal(false)}
        aria-labelledby="Audio"
        aria-describedby="Audio Player Modal">
        <Box sx={style}>
          <AudioWaveForm buffer={recordpath} />
        </Box>
      </Modal>
    </div>
  );
};

export default DataTable;
