import React from 'react';
import { HourglassFull } from '@material-ui/icons';
import CmtCard from '@coremat/CmtCard';
import CmtCardHeader from '@coremat/CmtCard/CmtCardHeader';
import { Typography } from '@material-ui/core';
import IntlMessages from '@jumbo/utils/IntlMessages';
const Ready = ({ stat }) => {
  const array = stat.filter(function(a) {
    return a.states !== 'Login' && a.states !== 'State' && a.states !== 'Logout';
  });
  return (
    <CmtCard>
      <CmtCardHeader
        icon={<HourglassFull fontSize="medium" style={{ color: '#C14545' }} />}
        title={
          <Typography style={{ fontFamily: 'Open Sans', color: '#C14545', fontWeight: 'bolder' }}>
            <IntlMessages id={'box.break'} />: {array.length}
          </Typography>
        }
      />
    </CmtCard>
  );
};

export default Ready;
