import React, { useState } from 'react';
import { Page, Document } from 'react-pdf/dist/esm/entry.webpack';
import sample from './sample.pdf';
import { Button } from '@material-ui/core';
import { ChevronLeft, ChevronRight } from '@material-ui/icons';
import Progress from '../routes/Pages/Progress';

const DocumentPage = () => {
  const [numPages, setNumPages] = useState(null);
  const [pageNumber, setPageNumber] = useState(1);
  function onDocumentLoadSuccess({ numPages }) {
    setNumPages(numPages);
  }

  return (
    <div>
      <Document file={{ url: sample }} onLoadSuccess={onDocumentLoadSuccess} loading={<Progress />}>
        <Page pageNumber={pageNumber} />
      </Document>
      <div style={{ display: 'flex' }}>
        <Button disabled={pageNumber === 1 ? true : false} onClick={() => setPageNumber(pageNumber - 1)}>
          <ChevronLeft />
        </Button>
        <p>
          Page {pageNumber} of {numPages}
        </p>
        <Button disabled={pageNumber === numPages ? true : false} onClick={() => setPageNumber(pageNumber + 1)}>
          <ChevronRight />
        </Button>
      </div>
    </div>
  );
};

export default DocumentPage;
