import React from 'react';
import Hidden from '@material-ui/core/Hidden';
import { Box } from '@material-ui/core';
import { NavLink } from 'react-router-dom';
import CmtImage from '../../../../@coremat/CmtImage';

const Logo = ({ color, ...props }) => {
  const logoUrl = color === 'white' ? '/images/logo-white.png' : '/images/logo-white.png';
  const logoSymbolUrl = color === 'white' ? '/images/logo-white.png' : '/images/logo-white.png';

  return (
    <Box className="pointer" {...props}>
      <Hidden xsDown>
        <NavLink to="/" style={{ marginLeft: '30%', marginTop: '10px' }}>
          <CmtImage style={{ backgroundColor: 'white', borderRadius: '9px' }} src={logoUrl} alt="logo" />
        </NavLink>
      </Hidden>
      <Hidden smUp>
        <NavLink to="/">
          <CmtImage style={{ backgroundColor: 'white', borderRadius: '9px' }} src={logoSymbolUrl} alt="logo" />
        </NavLink>
      </Hidden>
    </Box>
  );
};

export default Logo;
