import React from 'react';
import { Star, StarBorder } from '@material-ui/icons';
import IntlMessages from '@jumbo/utils/IntlMessages';

const RatingBoard = ({ tags, id }) => {
  const array = tags.filter(value => {
    return value.call_id === id;
  });
  let rate = [];

  if (array.length > 0) {
    if (array[0].rating !== '') {
      for (var j = 0; j < array[0].rating; j++) {
        rate.push({ id: j, icon: <Star style={{ color: '#F48B29' }} /> });
      }
      for (var i = 0; i < 5 - array[0].rating; i++) {
        rate.push({ id: i, icon: <StarBorder style={{ color: '#F48B29' }} /> });
      }
    }
  }
  return (
    <>
      {rate.length > 0 ? (
        rate.map(value => (
          <div style={{ display: 'inline-block' }} key={value.id}>
            {value.icon}
          </div>
        ))
      ) : (
        <p style={{ fontFamily: 'Open Sans' }}>
          {' '}
          <IntlMessages id={'no.rated'} />
        </p>
      )}
    </>
  );
};

export default RatingBoard;
