import React, { useState } from 'react';
import TextField from '@material-ui/core/TextField';
import IntlMessages from '../../../utils/IntlMessages';
import { useDispatch } from 'react-redux';
import Button from '@material-ui/core/Button';
import { Box } from '@material-ui/core';
import { AuhMethods } from '../../../../services/auth';
import ContentLoader from '../../ContentLoader';
import { alpha, makeStyles } from '@material-ui/core/styles';
import CmtImage from '../../../../@coremat/CmtImage';
import { CurrentAuthMethod } from '../../../constants/AppConstants';
import AuthWrapper from './AuthWrapper';
import { useHistory } from 'react-router';
import * as api from 'Api/api';
import { NotificationContainer, NotificationManager } from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import LanguageSwitcher from '@jumbo/components/AppLayout/partials/LanguageSwitcher';

const useStyles = makeStyles(theme => ({
  authThumb: {
    backgroundColor: alpha(theme.palette.primary.main, 0.12),
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    padding: 20,
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: '50%',
      order: 2,
    },
  },

  authContent: {
    padding: 30,
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: props => (props.variant === 'default' ? '50%' : '100%'),
      order: 1,
    },
    [theme.breakpoints.up('xl')]: {
      padding: 50,
    },
  },
  titleRoot: {
    marginBottom: 14,
    color: theme.palette.text.primary,
  },
  textFieldRoot: {
    '& .MuiOutlinedInput-notchedOutline': {
      borderColor: alpha(theme.palette.common.dark, 0.12),
    },
  },
  formcontrolLabelRoot: {
    '& .MuiFormControlLabel-label': {
      [theme.breakpoints.down('xs')]: {
        fontSize: 12,
      },
    },
  },
}));

const SignIn = ({ method = CurrentAuthMethod, variant = 'default', wrapperVariant = 'default' }) => {
  let history = useHistory();
  const [param, setParam] = useState({
    name: '',
    password: '',
    type: 'console',
  });
  const dispatch = useDispatch();
  const [loginState, setLoginState] = useState();

  let data = [];
  var myDate = new Date();
  myDate.setDate(myDate.getDate() + 1);
  data.push(myDate);

  const classes = useStyles({ variant });

  const onSubmit = () => {
    api
      .getUserAuth(param)
      .then(resp => {
        if (resp.status === 200) {
          setLoginState(resp.status);
          console.log(resp.data.data.token);
          localStorage.setItem('token', resp.data.data.token);
          history.push('/queues');
          window.location.reload();
        }
      })
      .catch(error => {
        NotificationManager.error(<IntlMessages id={'loginError'} />, '', 3000);
      });
  };

  function addNotification() {
    onSubmit();
  }

  if (loginState === 200) {
    localStorage.setItem('login', loginState);
    localStorage.setItem('agent', param.name);
  }

  return (
    <AuthWrapper variant={wrapperVariant}>
      <NotificationContainer />

      {variant === 'default' ? (
        <Box className={classes.authThumb}>
          <CmtImage src={'/images/auth/login-img.png'} />
        </Box>
      ) : null}
      <Box className={classes.authContent}>
        <Box mb={7}>
          <CmtImage src={'/images/favicon.png'} />
        </Box>

        <form style={{ fontFamily: 'Open Sans' }}>
          <Box mb={2}>
            <TextField
              label={<IntlMessages id={'agent.number'} />}
              fullWidth
              onChange={event =>
                setParam({
                  name: event.target.value,
                  password: param.password,
                  type: param.type,
                })
              }
              margin="normal"
              variant="outlined"
              className={classes.textFieldRoot}
            />
          </Box>
          <Box mb={2}>
            <TextField
              type="password"
              label={<IntlMessages id={'password'} />}
              fullWidth
              onChange={event =>
                setParam({
                  name: param.name,
                  password: event.target.value,
                  type: param.type,
                })
              }
              margin="normal"
              variant="outlined"
              className={classes.textFieldRoot}
            />
          </Box>
          <div style={{ textAlign: 'center' }}>
            <LanguageSwitcher />
          </div>

          <Box display="flex" alignItems="center" justifyContent="space-between" mb={5}>
            <Button
              onClick={() => {
                addNotification();
              }}
              variant="contained"
              color="primary">
              <IntlMessages id="login" />
            </Button>
          </Box>
        </form>

        {dispatch(AuhMethods[method].getSocialMediaIcons())}

        <ContentLoader />
      </Box>
    </AuthWrapper>
  );
};

export default SignIn;
