import React from 'react';
import CounterCard from '@jumbo/components/Common/CounterCard';
import { Restaurant } from '@material-ui/icons';
import IntlMessages from '@jumbo/utils/IntlMessages';
const YEMEK = ({ data, value }) => {
  const breaknum = data.filter(function(b) {
    return b.q_num === value && b.status === 'YEMEK';
  });

  return (
    <CounterCard
      icon={<Restaurant />}
      number={
        breaknum.length > 0
          ? breaknum.map(value => {
              return value.escapetime;
            })
          : '0d 00:00:00'
      }
      label={<IntlMessages id={'meal'} />}
      labelProps={{
        fontSize: 12,
      }}
      backgroundColor={['#6166B3 -18.96%', '#544179 108.17%']}
      gradientDirection="180deg"
    />
  );
};

export default YEMEK;
