import React from 'react';
import CounterCard from '@jumbo/components/Common/CounterCard';
import { Group } from '@material-ui/icons';
import IntlMessages from '@jumbo/utils/IntlMessages';

const TOPLANTI = ({ data, value }) => {
  const breaknum = data.filter(function(b) {
    return b.q_num === value && b.status === 'TOPLANTI';
  });

  return (
    <CounterCard
      icon={<Group />}
      number={
        breaknum.length > 0
          ? breaknum.map(value => {
              return value.escapetime;
            })
          : '0d 00:00:00'
      }
      label={<IntlMessages id={'meeting'} />}
      labelProps={{
        fontSize: 12,
      }}
      backgroundColor={['#ADCE74 -18.96%', '#4E8D7C 108.17%']}
      gradientDirection="180deg"
    />
  );
};

export default TOPLANTI;
