import React, { useEffect, useRef, useState } from 'react';
import WaveSurfer from 'wavesurfer.js';
import Slider from '@material-ui/core/Slider';
import VolumeDown from '@material-ui/icons/VolumeDown';
import VolumeUp from '@material-ui/icons/VolumeUp';
import PauseRounded from '@material-ui/icons/PauseRounded';
import PlayArrowRounded from '@material-ui/icons/PlayArrowRounded';
import IconButton from '@material-ui/core/IconButton';
import CircularProgress from '@material-ui/core/CircularProgress';
import Box from '@material-ui/core/Box';
import WaveFile from 'wavefile';

const formWaveSurferOptions = ref => ({
  container: ref,
  waveColor: '#9cceff',
  progressColor: '#1976d2',
  cursorColor: '#1976d2',
  barWidth: 2,
  barRadius: 3,
  barHeight: 1,
  barGap: 2,
  responsive: true,
});

export default function AudioWaveForm({ buffer }) {
  const waveformRef = useRef(null);
  const wavesurfer = useRef(null);
  const [playing, setPlay] = useState(false);
  const [volume, setVolume] = useState(0.5);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    setPlay(false);

    const options = formWaveSurferOptions(waveformRef.current);
    wavesurfer.current = WaveSurfer.create(options);

    let wav = new WaveFile(new Uint8Array(buffer));
    wav.fromIMAADPCM();
    let newBuf = wav.toBuffer();
    var blob = new Blob([newBuf], { type: 'audio/wav' });
    wavesurfer.current.loadBlob(blob);

    wavesurfer.current.on('ready', function() {
      setLoading(false);
      if (wavesurfer.current) {
        wavesurfer.current.setVolume(0.5);
      }
    });
    return () => wavesurfer.current.destroy();
  }, [buffer]);

  const handlePlayPause = () => {
    setPlay(!playing);
    wavesurfer.current.playPause();
  };

  const onVolumeChange = (e, newVal) => {
    if (newVal) {
      setVolume(newVal);
      wavesurfer.current.setVolume(newVal || 1);
    }
  };

  return (
    <div>
      {loading && (
        <Box sx={{ display: 'flex', justifyContent: 'center' }}>
          <CircularProgress />
        </Box>
      )}
      <div id="waveform" ref={waveformRef} />
      <div
        style={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center',
        }}>
        <IconButton aria-label={!playing ? 'play' : 'pause'} onClick={handlePlayPause} disabled={loading}>
          {!playing ? (
            <PlayArrowRounded sx={{ fontSize: '3rem' }} htmlColor="#000" />
          ) : (
            <PauseRounded sx={{ fontSize: '3rem' }} htmlColor="#000" />
          )}
        </IconButton>
        <div
          style={{
            display: 'flex',
            marginBottom: '1rem',
            width: '300px',
            alignItems: 'center',
          }}>
          <VolumeDown />
          <Slider
            id="volume"
            name="volume"
            aria-label="Volume"
            value={volume}
            onChange={onVolumeChange}
            min={0.01}
            max={1}
            step={0.025}
            disabled={loading}
          />
          <VolumeUp />
        </div>
      </div>
    </div>
  );
}
