import React, { useEffect } from 'react';
import { Popover, Button, Box } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import AddNote from './addNote';
const Popup = ({ id, tags, setMsg }) => {
  let array = tags.filter(value => {
    return value.call_id === id;
  });
  useEffect(() => {
    setNote(array.length > 0 ? array[0].note : '');
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [tags]);
  const useStyles = makeStyles(theme => ({
    typography: {
      padding: theme.spacing(4),
      minWidth: 50,
    },
  }));

  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [note, setNote] = React.useState(array.length > 0 ? array[0].note : '');
  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const id_ = open ? 'simple-popover' : undefined;

  return (
    <>
      <Box>
        <Button aria-describedby={id} variant="text" color="primary" onClick={handleClick}>
          {note === '' ? 'Add Note' : note}
        </Button>

        <Popover
          id={id_}
          open={open}
          anchorEl={anchorEl}
          onClose={handleClose}
          anchorOrigin={{
            vertical: 'top',
            horizontal: 'center',
          }}
          transformOrigin={{
            vertical: 'bottom',
            horizontal: 'center',
          }}>
          <Box className={classes.typography}>
            <AddNote setMsg={setMsg} id={id} setNote={setNote} popupClose={handleClose} />
          </Box>
        </Popover>
      </Box>
    </>
  );
};

export default Popup;
