import React from 'react';
import GridContainer from '@jumbo/components/GridContainer';
import { Divider, Grid } from '@material-ui/core';
import YEMEK from './YEMEK';
import QueueFilter from './Filter';
import * as api from 'Api/api';
import TOPLANTI from './TOPLANTI';
import Ekstra1 from './Extra1';
import Ekstra2 from './Extra2';
import Login from './Login';
import Logout from './Logout';
import SpringModal from 'routes/Pages/Modal';

const Breakout = ({ queues, value, setValue, online, setOnline, chosen, setChosen }) => {
  const agent = localStorage.getItem('agent');
  const [open, setOpen] = React.useState(false);
  const [lastAction, setLastAction] = React.useState([]);
  const [period, setPeriod] = React.useState([]);
  const [breakname, setBreak] = React.useState('');
  const [response, setResponse] = React.useState([]);

  React.useEffect(() => {
    api.showBreaks(agent).then(resp => {
      setResponse([...JSON.parse(resp.data)]);
    });
  }, [agent]);

  return (
    <GridContainer>
      <SpringModal
        lastAction={lastAction}
        period={period}
        setPeriod={setPeriod}
        setOpen={setOpen}
        open={open}
        breakname={breakname}
      />
      <Grid item xs={12} md={12} lg={12}>
        <QueueFilter
          setOpen={setOpen}
          setChosen={setChosen}
          setPeriod={setPeriod}
          breakname={breakname}
          chosen={chosen}
          lastAction={lastAction}
          setLastAction={setLastAction}
          setBreak={setBreak}
          queues={queues}
          online={online}
          response={response}
          setOnline={setOnline}
          value={value}
          setValue={setValue}
          setResponse={setResponse}
        />
      </Grid>
      <Grid item xs={12} sm={12} md={12} lg={12}>
        <GridContainer>
          <Grid item xs={12} sm={6} md={2}>
            <YEMEK data={response} value={value} />
          </Grid>

          <Grid item xs={12} sm={6} md={2}>
            <TOPLANTI data={response} value={value} />
          </Grid>

          <Grid item xs={12} sm={6} md={2}>
            <Ekstra1 data={response} value={value} />
          </Grid>
          <Grid item xs={12} sm={6} md={2}>
            <Ekstra2 data={response} value={value} />
          </Grid>
          <Grid item xs={12} sm={6} md={2}>
            <Login data={response} value={value} />
          </Grid>
          <Grid item xs={12} sm={6} md={2}>
            <Logout data={response} value={value} />
          </Grid>
          <Grid item xs={12} md={12} lg={12}>
            <Divider style={{ width: '100%' }} />
          </Grid>
        </GridContainer>
      </Grid>
    </GridContainer>
  );
};

export default Breakout;
