import GridContainer from '@jumbo/components/GridContainer';
import React, { useEffect, useState } from 'react';
import ChartData from './ChartData';
import { Grid } from '@material-ui/core';
import * as api from 'Api/api';
import CallDurations from './CallDurations';
import Statistics from './Statistics';

const ChartPage = () => {
  const [breaktimes, setBreakTimes] = useState([]);
  const [breaknames, setBreakNames] = useState([]);
  useEffect(() => {
    api.getBreakNums().then(resp => {
      setBreakNames([...JSON.parse(resp.data)]);
    });
  }, []);
  useEffect(() => {
    api.getBreakChartData().then(resp => {
      setBreakTimes([...JSON.parse(resp.data.data)]);
    });
  }, []);
  function renameProperty(obj, fromKey, toKey) {
    obj[toKey] = obj[fromKey];
    delete obj[fromKey];
  }
  if (breaknames.length > 0 && breaktimes.length > 0) {
    const keys = breaktimes.flatMap(Object.keys);
    for (let i = 0; i < breaknames.length; i++) {
      // console.log(keys[i]);
      if (keys[i].includes(breaknames[i].value)) {
        breaktimes.forEach(obj => renameProperty(obj, keys[i], breaknames[i].name));
      }
    }
    // console.log(obj);
  }
  console.log(breaktimes);
  return (
    <GridContainer>
      <Grid item xs={12} md={12} lg={12}>
        <CallDurations />
      </Grid>
      <Grid item xs={12} md={6} lg={6} xl={6}>
        <ChartData data={breaktimes} />
      </Grid>
      <Grid item xs={12} md={6} lg={6} xl={6}>
        <Statistics />
      </Grid>
    </GridContainer>
  );
};

export default ChartPage;
