import React, { useState, useEffect } from 'react';
import { Card, CardActions, FormControl, InputLabel, Select, MenuItem, FormHelperText, Grid } from '@material-ui/core';
import GridContainer from '@jumbo/components/GridContainer';
import DropdownMenu from 'routes/Pages/AgentStatuses/Dropdown';
import IntlMessages from '@jumbo/utils/IntlMessages';
import * as api from 'Api/api';

const QueueFilter = ({
  queues,
  chosen,
  setChosen,
  value,
  setValue,
  setOpen,
  response,
  setLastAction,
  lastAction,
  setResponse,
  setPeriod,
  isChecked,
  breakname,
  setBreak,
  setChecked,
  online,
  setOnline,
}) => {
  const handleChange = event => {
    setValue(event.target.value);
  };
  const handleTagChange = event => {
    setChosen(event.target.value);
    var id = department.find(d => d.name === event.target.value)?.id;
    setSelectedDepId(id);
  };
  const [department, setDepartment] = useState([]);

  useEffect(() => {
    api.showTags().then(resp => setDepartment(JSON.parse(resp.data)));
  }, []);

  //agenta ait kuyruklar id ile
  const [agentsQueueWithId, setAgentsQueueWithId] = useState([]);
  //seçili departman id
  const [selectedDepId, setSelectedDepId] = useState(0);

  const [filteredQ,setFilteredQ] = useState([]);

  useEffect(()=>{
    //tüm kuyrukları getir. içinde id de var
    api.getQueues().then(resp => {
      let temp = JSON.parse(resp.data.data);
      let tempArr = []
      for(var i = 0;i<queues.length;i++){
        for(var j = 0;j<temp.length;j++){
          if(queues[i].queue_dn === temp[j].dn){
            tempArr.push(temp[j])
          }
        }        
      }
      setAgentsQueueWithId(tempArr);
    })
  },[queues]);

  useEffect(()=>{
    api.getDepOwnedQueues(selectedDepId).then(result => {
      let tempArr = [];
      let resArr = JSON.parse(result.data.data);
      for (let i = 0; i < resArr.length; i++) {
        for(let j = 0;j<agentsQueueWithId.length;j++){
          if(resArr[i].queue_id === agentsQueueWithId[j].id){
            tempArr.push(agentsQueueWithId[j])
          }
        }
      }
      setFilteredQ(tempArr);
    });
  },[selectedDepId, agentsQueueWithId])

  return (
    <Card>
      <CardActions>
        <GridContainer>
          <Grid item xs={4} md={4} lg={4}>
            <FormControl fullWidth>
              <InputLabel id="dl">Department</InputLabel>
              <Select labelId="dl" id="dl" value={chosen} label="Department" onChange={handleTagChange}>
                {department.length > 0 &&
                  department.map((value, index) => (
                    <MenuItem key={index} value={value.name}>
                      {value.name}
                    </MenuItem>
                  ))}
              </Select>
              <FormHelperText> </FormHelperText>
            </FormControl>
          </Grid>

          {filteredQ.length > 0 ? (
            <>
              <Grid item xs={4} md={4} lg={4}>
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-helper-label">
                    {' '}
                    <IntlMessages id={'select.queue'} />
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-helper-label"
                    id="demo-simple-select-helper"
                    value={value}
                    label="Age"
                    onChange={handleChange}>
                    {filteredQ.length > 0 &&
                      filteredQ.map((value, index) => (
                        <MenuItem key={index} value={value.dn}>
                          {value.dn} {value.display_name}
                        </MenuItem>
                      ))}
                  </Select>
                  <FormHelperText>
                    <IntlMessages id={'select.warning'} />
                  </FormHelperText>
                </FormControl>
              </Grid>
            </>
          ) : (
            ''
          )}

          <Grid item xs={4} md={4} lg={4}>
            <DropdownMenu
              isChecked={isChecked}
              online={online}
              breakname={breakname}
              setOnline={setOnline}
              setChecked={setChecked}
              setOpen={setOpen}
              lastAction={lastAction}
              setLastAction={setLastAction}
              setPeriod={setPeriod}
              setBreak={setBreak}
              response={response}
              queues={queues}
              setResponse={setResponse}
            />
          </Grid>
        </GridContainer>
      </CardActions>
    </Card>
  );
};

export default QueueFilter;
