import React from 'react';
import CounterCard from '@jumbo/components/Common/CounterCard';
import { Book } from '@material-ui/icons';
import IntlMessages from '@jumbo/utils/IntlMessages';

const Ekstra2 = ({ data, value }) => {
  const breaknum = data.filter(function(b) {
    return b.q_num === value && b.status === 'Ekstra 2';
  });

  return (
    <CounterCard
      icon={<Book />}
      number={
        breaknum.length > 0
          ? breaknum.map(value => {
              return value.escapetime;
            })
          : '0d 00:00:00'
      }
      label={<IntlMessages id={'education'} />}
      labelProps={{
        fontSize: 12,
      }}
      backgroundColor={['#EA4C4C -18.96%', '#C14545 108.17%']}
      gradientDirection="180deg"
    />
  );
};

export default Ekstra2;
