import React, { useEffect,useContext } from 'react';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import { Box, InputLabel } from '@material-ui/core';
import PhoneMissed from '@material-ui/icons/PhoneMissed';
import * as api from 'Api/api';

import IntlMessages from '@jumbo/utils/IntlMessages';
import AppContext from '@jumbo/components/contextProvider/AppContextProvider/AppContext';
const DropdownMenu = ({
  queues,
  setResponse,
  response,
  breakname,
  setOnline,
  setOpen,
  setBreak,
  setPeriod,
  lastAction,
  setLastAction,
}) => {
  const {anchorEl, setAnchorEl,setBreakBtn} = useContext(AppContext);
  // const [anchorEl, setAnchorEl] = React.useState(null);
  const [value, setValue] = React.useState([]);
  const [chosenBreak, setChosenBreak] = React.useState('');

  const agent = localStorage.getItem('agent');

  useEffect(() => {
    api.getBreakNums().then(resp => setValue([...JSON.parse(resp.data)]));
    api.showLastAction().then(resp => {
      setLastAction([...JSON.parse(resp.data)]);
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  useEffect(() => {
    api.getLogoutTime(chosenBreak).then(resp => {
      setPeriod([...JSON.parse(resp.data)]);
    });
    return () => {
      setPeriod([]);
    };
  }, [chosenBreak, setPeriod]);

  //mola seç butonu tıklanması
  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };
  useEffect(()=>{
    var breakButton = document.querySelector('#breakButton');
    setBreakBtn(breakButton);
  },[setBreakBtn]);
  //mola seçimi
  const handleClose = value => {
    setAnchorEl(null);
    const reg = /^\d+$/;
    if (reg.test(value)) {
      setChosenBreak(value);
      api.changeSessionStatus(agent).then(resp => {
        api
          .insertBreak(queues, value, agent)
          .then(resp => {
            api.showBreaks(agent).then(resp => setResponse([...JSON.parse(resp.data)]));
          })
          .then(() => {
            api
              .qstatus({ agentDn: agent, token: localStorage.getItem('token') })
              .then(resp => setOnline(resp))
              .then(() => {
                api.showLastAction().then(resp => setLastAction([...JSON.parse(resp.data)]));
                setOpen(true);
              });
          });
      });
    }
  };
  //mesai sonu seçimi
  const handleEndOfShift = () => {
    setAnchorEl(null);
    api.changeSessionStatus(agent).then(() => {
      api.qstatus({ agentDn: agent, token: localStorage.getItem('token') }).then(resp => setOnline(resp));
      setLastAction([]);
    });
  };
  // eslint-disable-next-line
  const arr = value.filter(function(a) {
    if (lastAction.length > 0) {
      return a.value === lastAction[0].is_loggedin_in_queue;
    }
  });

  useEffect(() => {
    if (arr.length > 0) {
      setBreak(arr[0].name);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [arr, lastAction]);

  return (
    <Box>
      <Button
      id="breakButton"
        variant="outlined"
        color="secondary"
        style={{ color: '#C14545', borderColor: '#C14545' }}
        aria-controls="simple-menu"
        aria-haspopup="true"
        onClick={handleClick}>
        <PhoneMissed style={{ marginRight: '6px' }} />
        <IntlMessages id="breakinput" />
      </Button>
      <Menu id="simple-menu" anchorEl={anchorEl} keepMounted open={Boolean(anchorEl)} onClose={handleClose}>
        <MenuItem onClick={() => handleEndOfShift()}>
          <IntlMessages id={'shift.end'} />
        </MenuItem>
        {value.map((item, index) => (
          <MenuItem key={index} onClick={() => handleClose(item.value)}>
            {item.value} {item.name}
          </MenuItem>
        ))}
      </Menu>
      <InputLabel style={{ fontFamily: 'Open Sans', marginTop: '7px' }}>
      <IntlMessages id={'last.break.type'} />:<strong> {lastAction.length && arr.length > 0 > 0 ? arr[0].name : 'Mola Yok'}</strong>
      </InputLabel>
    </Box>
  );
};
export default DropdownMenu;
