import React, { useState, useEffect, useContext } from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import { Button, Grid } from '@material-ui/core';
import Paper from '@material-ui/core/Paper';
import { Switch, Typography } from '@material-ui/core';
import * as api from 'Api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';
import AppContext from '@jumbo/components/contextProvider/AppContextProvider/AppContext';
const DenseTable = ({ data, isChecked, setChecked, setOnline }) => {
  let [chosenList, setChosenList] = useState(data);
  const {setAnchorEl, breakBtn} = useContext(AppContext);

  useEffect(() => {
    setChosenList(data);
  }, [data]);

  const useStyles = makeStyles(theme => ({
    root: {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
      fontFamily: 'Open Sans',
    },
    table: {
      minWidth: 650,
      boxShadow: 3,
      border: '1px',
    },
    tableHead: {
      fontWeight: 'bolder',
      fontSize: 14,
      color: theme.palette.common.white,
      backgroundColor: theme.palette.background.default,
    },
  }));

  const classes = useStyles();

  const handleChange = (idx, status) => {
    if(!status){
      setAnchorEl(breakBtn);
    }
    const updatedMember = chosenList[idx];
    updatedMember.isChosen = status;
    const newMember = [...chosenList];
    newMember[idx] = updatedMember;
    setChosenList(newMember);

    const agent = localStorage.getItem('agent').toString();
    const token = localStorage.getItem('token').toString();

    let body = {};

    for (let i = 0; i < chosenList.length; i++) {
      if (chosenList[i].isChosen === true) {
        body = { Action: 'login_only', agentDn: agent, token: token, queue: chosenList[i].queue_number };
        api.loginQueue(body).then(() => {
          api.qstatus({ agentDn: agent, token: token }).then(resp => setOnline(resp));
        });
        setChecked(true);
      } else {
        body = { Action: 'logout_only', agentDn: agent, token: token, queuenumber: chosenList[i].queue_number };
        api.logoutQueue(body).then(() => api.qstatus({ agentDn: agent, token: token }).then(resp => setOnline(resp)));
      }
    }
  };


  const handleLoginAll = () => {
    let body = {
      Action: 'login_all',
      agentDn: localStorage.getItem('agent').toString(),
    };
    api.loginAllQueue(body).then(resp => {
        window.location.reload();
    });
  };

  const handleLogoutAll = () => {
    setAnchorEl(breakBtn);
  };

  return (
    <TableContainer component={Paper} className={classes.root}>
      <Button onClick={handleLoginAll} variant="contained" color="primary">
        <IntlMessages id="loginall" />
      </Button>
      <Button style={{ marginLeft: '1rem' }} onClick={handleLogoutAll} variant="contained" color="primary">
        <IntlMessages id="logoutall" />
      </Button>
      <Table className={classes.table} size="small" aria-label="a dense table">
        <TableHead className={classes.tableHead}>
          <TableRow>
            <TableCell align="center">
              <IntlMessages id={'queue.number'} />
            </TableCell>
            <TableCell align="center">
              <IntlMessages id={'queue.name'} />
            </TableCell>
            <TableCell align="left">
              <IntlMessages id={'queue.status'} />
            </TableCell>
          </TableRow>
        </TableHead>

        <TableBody>
          {chosenList.map((row, i) => (
            <TableRow key={i}>
              <TableCell align="center">{row.queue_number}</TableCell>
              <TableCell align="center">{row.queue_name}</TableCell>
              <TableCell align="center">
                <Typography component={'span'}>
                  <Grid component="label" container alignItems="flex-end" spacing={1}>
                    <Grid item>
                      <IntlMessages id={'off'} />
                    </Grid>
                    <Grid item>
                      <Switch
                        checked={row.isChosen && isChecked}
                        color="primary"
                        onChange={() => {
                          handleChange(i, !row.isChosen);
                        }}
                      />
                    </Grid>
                    <Grid item>
                      <IntlMessages id={'on'} />
                    </Grid>
                  </Grid>
                </Typography>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
};
export default DenseTable;
